import { ResizeObserver } from 'vue-resize';
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css';
import { CalendarView, CalendarViewHeader, CalendarMathMixin } from 'vue-simple-calendar';
import Vuetable from 'vuetable-2/src/components/Vuetable';
import VuetablePaginationBootstrap from '@/components/Common/VuetablePaginationBootstrap';
import IconCard from '@/components/Cards/IconCard';
import LineShadowChart from '@/components/Charts/LineShadow';
import DoughnutShadowChart from '@/components/Charts/DoughnutShadow';
import RecentOrderItem from '@/components/Listing/RecentOrderItem';
import ListWithUserItem from '@/components/Listing/ListWithUserItem';
import TwoColumnList from '@/components/Listing/TwoColumnList';
import { lineChartData, doughnutChartData } from '@/data/charts';
import products from '@/data/products';
import tickets from '@/data/tickets';
import cakes from '@/data/cakes';
import { apiUrl } from '@/constants/config';
export default {
  components: {
    IconCard: IconCard,
    swiper: swiper,
    swiperSlide: swiperSlide,
    ResizeObserver: ResizeObserver,
    LineShadowChart: LineShadowChart,
    RecentOrderItem: RecentOrderItem,
    ListWithUserItem: ListWithUserItem,
    CalendarView: CalendarView,
    CalendarViewHeader: CalendarViewHeader,
    Vuetable: Vuetable,
    VuetablePaginationBootstrap: VuetablePaginationBootstrap,
    TwoColumnList: TwoColumnList,
    DoughnutShadowChart: DoughnutShadowChart
  },
  mixins: [CalendarMathMixin],
  data: function data() {
    return {
      swiperIconsOption: {
        slidesPerView: 4,
        breakpoints: {
          0: {
            slidesPerView: 1
          },
          320: {
            slidesPerView: 1
          },
          576: {
            slidesPerView: 2
          },
          1800: {
            slidesPerView: 3
          }
        }
      },
      lineChartData: lineChartData,
      doughnutChartData: doughnutChartData,
      products: products,
      cakes: cakes,
      tickets: tickets,
      calendar: {
        showDate: this.thisMonth(1),
        events: [{
          id: 'e2',
          startDate: this.thisMonth(15),
          title: 'Meeting',
          classes: 'secondary'
        }, {
          id: 'e3',
          startDate: this.thisMonth(8, 9, 25),
          endDate: this.thisMonth(9, 16, 30),
          title: 'Sales',
          classes: 'primary'
        }, {
          id: 'e5',
          startDate: this.thisMonth(5),
          endDate: this.thisMonth(12),
          title: 'Tax Days',
          classes: 'secondary'
        }, {
          id: 'e10',
          startDate: this.thisMonth(27),
          title: 'My Birthday!'
        }]
      },
      bestsellers: {
        apiUrl: apiUrl + '/cakes/fordatatable',
        fields: [{
          name: 'title',
          sortField: 'title',
          title: 'Name',
          titleClass: '',
          dataClass: 'list-item-heading'
        }, {
          name: 'sales',
          sortField: 'sales',
          title: 'Sales',
          titleClass: '',
          dataClass: 'text-muted'
        }, {
          name: 'stock',
          sortField: 'stock',
          title: 'Stock',
          titleClass: '',
          dataClass: 'text-muted'
        }, {
          name: 'category',
          sortField: 'category',
          title: 'Category',
          titleClass: '',
          dataClass: 'text-muted'
        }]
      }
    };
  },
  mounted: function mounted() {
    var _this = this;

    setTimeout(function () {
      _this.handleSwipersResize();
    }, 50);
  },
  methods: {
    refreshButtonClick: function refreshButtonClick() {
      console.log('refreshButtonClick');
    },
    handleSwipersResize: function handleSwipersResize() {
      this.$refs.swiperIcons.update();
    },
    thisMonth: function thisMonth(d, h, m) {
      var t = new Date();
      return new Date(t.getFullYear(), t.getMonth(), d, h || 0, m || 0);
    },
    onClickDay: function onClickDay(d) {
      console.log("You clicked: ".concat(d.toLocaleDateString()));
    },
    onClickEvent: function onClickEvent(e) {
      console.log("You clicked: ".concat(e.title));
    },
    setShowDate: function setShowDate(d) {
      this.calendar.showDate = d;
    },
    onDropDate: function onDropDate(event, date) {
      console.log("You dropped ".concat(event.id, " on ").concat(date.toLocaleDateString()));
      var eLength = this.dayDiff(event.startDate, date);
      event.originalEvent.startDate = this.addDays(event.startDate, eLength);
      event.originalEvent.endDate = this.addDays(event.endDate, eLength);
    },
    onPaginationData: function onPaginationData(paginationData) {
      this.$refs.pagination.setPaginationData(paginationData);
    },
    onChangePage: function onChangePage(page) {
      this.$refs.vuetable.changePage(page);
    }
  }
};